@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
/* General Body Styling */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Header and Navbar Styling */
nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: lightblue;
  padding: 12px;
  flex-wrap: wrap;
}

nav h1 {
  font-size: 24px;
  font-weight: bold;
}

nav ul {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  list-style: none;
  padding: 0;
}

nav ul a {
  font-weight: 600;
  font-size: 17px;
  color: black;
  text-decoration: none;
}

nav ul a:hover {
  text-decoration: underline;
}

nav .searchBar {
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

nav .searchBar input {
  width: 250px;
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

nav .searchBar button {
  width: 80px;
  height: 40px;
  background-color: blue;
  border: none;
  border-radius: 4px;
  color: white;
  font-size: 16px;
  cursor: pointer;
}

nav .searchBar button:hover {
  background-color: darkblue;
}

/* Heading Styling */
.head {
  text-align: center;
  font-weight: 600;
  font-size: 28px;
  margin: 20px 0;
}

/* Category Buttons */
.categoryBtn {
  display: flex;
  justify-content: center;
  grid-gap: 15px;
  gap: 15px;
  margin-bottom: 20px;
}

.categoryBtn button {
  width: 130px;
  height: 40px;
  background-color: rgb(255, 108, 108);
  border: none;
  border-radius: 16px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.categoryBtn button:hover {
  background-color: rgb(230, 90, 90);
}

/* Card Container Styling */
.cardContainer {
  display: flex;
  justify-content: center;
  grid-gap: 25px;
  gap: 25px;
  flex-wrap: wrap;
  margin: 20px 10px;
}

/* Card Styling */
.card {
  width: 320px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: white;
  border-radius: 8px;
  border: 1px solid #ddd;
  overflow: hidden;
  transition: transform 0.2s ease-in-out;
}

.card:hover {
  transform: translateY(-5px);
}

.card img {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.card .content {
  padding: 15px;
}

.card .title {
  font-weight: 600;
  font-size: 18px;
  color: #333;
  cursor: pointer;
  margin-bottom: 10px;
  text-decoration: none;
}

.card .title:hover {
  text-decoration: underline;
}

.card p {
  font-size: 14px;
  color: #555;
  margin-bottom: 10px;
}

.card button {
  background-color: #1da2d6;
  border: none;
  cursor: pointer;
  width: 100px;
  height: 30px;
  color: white;
  font-size: 14px;
  border-radius: 4px;
  transition: background-color 0.2s ease-in-out;
}

.card button:hover {
  background-color: #188bb3;
}

/* Media Queries - Mobile View Styling */
@media (max-width: 768px) {
  nav {
    flex-direction: column;
    align-items: flex-start;
    padding: 15px;
  }

  nav ul {
    flex-direction: column;
    align-items: flex-start;
  }

  nav ul a {
    margin-bottom: 10px;
    font-size: 16px;
  }

  .searchBar {
    flex-direction: row; /* Ensure input and button are in a row */
    grid-gap: 10px;
    gap: 10px; /* Add gap between input and button */
    width: 100%; /* Full width to ensure alignment */
  }

  .searchBar input {
    width: calc(100% - 90px); /* Adjust width to accommodate button */
    margin-bottom: 0; /* Remove margin-bottom to keep in line */
  }

  .searchBar button {
    width: 80px; /* Maintain button width */
  }

  .categoryBtn {
    flex-direction: column;
  }

  .categoryBtn button {
    width: 100%;
    margin-bottom: 10px;
  }

  .cardContainer {
    flex-direction: column;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
  }

  .card {
    width: 100%;
    max-width: 400px;
  }
}

@media (max-width: 480px) {
  nav h1 {
    font-size: 20px;
  }

  nav .searchBar input {
    font-size: 14px;
    width: calc(100% - 90px); /* Adjust width for smaller screens */
  }

  nav .searchBar button {
    font-size: 14px;
    width: 80px; /* Maintain button size */
  }

  .card .title {
    font-size: 16px;
  }

  .card p {
    font-size: 13px;
  }

  .card button {
    width: 90px;
    height: 28px;
    font-size: 13px;
  }
}
/* Responsive full-page CSS for CrimePage */

/* Base styles */
.crime-page {
    height: 100vh;
    width: 95vw;
    margin: 30px 0px;
    background-image: url("https://images.pexels.com/photos/923681/pexels-photo-923681.jpeg"); /* Replace with a real crime-related background image */
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    box-sizing: border-box;
}

.crime-content {
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    padding: 40px;
    border-radius: 10px;
    text-align: center;
    max-width: 600px;
    width: 100%;
    box-sizing: border-box;
}

.crime-content h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
}

.crime-content p {
    font-size: 1.2rem;
    line-height: 1.6;
    margin-bottom: 20px;
}

.crime-button {
    padding: 15px 30px;
    margin-top: 20px;
    font-size: 1.2rem;
    background-color: #ff0000;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.crime-button:hover {
    background-color: #cc0000;
}

/* Responsive adjustments */

/* Small devices (phones, portrait view) */
@media only screen and (max-width: 768px) {
    .crime-content {
        padding: 30px;
    }

    .crime-content h1 {
        font-size: 2rem;
    }

    .crime-content p {
        font-size: 1rem;
    }

    .crime-button {
        font-size: 1rem;
        padding: 12px 24px;
    }
}

/* Extra small devices (small phones) */
@media only screen and (max-width: 480px) {
    .crime-content {
        padding: 20px;
    }

    .crime-content h1 {
        font-size: 1.8rem;
    }

    .crime-content p {
        font-size: 0.9rem;
    }

    .crime-button {
        font-size: 0.9rem;
        padding: 10px 20px;
    }
}

/* Large devices (desktops, larger screens) */
@media only screen and (min-width: 1200px) {
    .crime-content {
        max-width: 800px;
    }

    .crime-content h1 {
        font-size: 3rem;
    }

    .crime-content p {
        font-size: 1.4rem;
    }

    .crime-button {
        font-size: 1.4rem;
        padding: 18px 36px;
    }
}
