/* Card Section */
.card-section {
    display: flex;
    justify-content: space-between; /* Space out cards evenly */
    align-items: stretch; /* Align cards to the same height */
    grid-gap: 30px;
    gap: 30px; /* Space between cards */
    margin-top: 40px;
    flex-wrap: nowrap; /* Prevent cards from wrapping to a new line */
}

/* Card Styling */
.user-card {
    background-color: #fff;
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    transition: transform 0.3s, box-shadow 0.3s;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1 1; /* Make cards flexible */
    max-width: 300px; /* Set a maximum width for cards */
}

.user-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 12px 20px rgba(0, 0, 0, 0.15);
}

.card-title {
    font-size: 22px;
    font-weight: 700;
    color: #333;
    margin-bottom: 15px;
}

.card-description {
    font-size: 16px;
    color: #555;
    margin-bottom: 20px;
}

.card-button {
    background-color: #ff9c1bfe;
    color: white;
    font-weight: 600;
    padding: 8px 16px;
    text-transform: none;
    border-radius: 4px;
    transition: background-color 0.3s;
    align-self: flex-start;
}

.card-button:hover {
    background-color: #e08914;
}

/* Responsive Styles */
@media (max-width: 768px) {
    .card-section {
        flex-direction: column; /* Stack cards vertically */
        grid-gap: 20px;
        gap: 20px;
    }

    .user-card {
        width: 100%; /* Make cards full width on smaller screens */
    }
}
